import React, {Component} from 'react'
import PropTypes from 'prop-types'
import Link from 'next/link'
import ItemStyles from '../components/styles/ItemStyles'
import Title from '../components/styles/Title';
import styled from 'styled-components';

const candAllInfo= styled.div`
display: grid;
grid-template-columns: repeat(2, 1fr);
`;

class Candidate extends Component {
    static propTypes = {
        eachCandidate: PropTypes.object.isRequired
    }

    render() {
        const {
            image,
            id,
            candCode,
            cand1stName,
            cand2ndName,
            cand3rdName,
            email,
            phoneNumb,
            placeOfBirth
        } = this.props.eachCandidate
        return (

            <ItemStyles>
                <candAllInfo>
         
                    <div className="candImg">
                        {image && <img src={image} alt={cand1stName}/>}
                    </div>
                    <Title>
                        <Link
                            href={{
                            pathname: "/candidatePage",
                            query: {
                                id: id
                            }
                        }}>
                            <a>
                                {cand1stName}
                            </a>
                        </Link>
                    </Title>
                    <div className="restInfo">
                        <div className="itemInfo">
                            <p>Phone:{phoneNumb}</p>
                        </div>
                        <div className="itemInfo">
                            <p>Name:{cand1stName}</p>
                        </div>
                        <div className="itemInfo">
                            <p>2nd Name:{cand2ndName}</p>
                        </div>
                        <div className="itemInfo">
                            <p>last Name{cand3rdName}</p>
                        </div>
                        <div className="itemInfo">
                            <p>Code:{candCode}</p>
                        </div>
                        <div className="itemInfo">
                            <p>Email:{email}</p>
                        </div>
                        <div className="itemInfo">
                            <p>Birth Place:{placeOfBirth}</p>
                        </div>
                    </div>
                    </candAllInfo>
                <div className="buttonList">
                    <Link
                        href={{
                        pathname: "/update",
                        query: {
                            id: id
                        }
                    }}>
                        <a>
                            Edit
                        </a>
                    </Link>
                    <button>Delete</button>
                </div>
            </ItemStyles>
        )
    }
}
export default Candidate;